@import "../../styles/utils.module.scss";

.page-header {
    background-color : $oc-blue-6;

    .content {
        height : 5rem;
        width : 1400px;
        margin : 0 auto;
        padding-left : 3rem;
        padding-right : 3rem;

        display : flex;
        align-items : center;

        @include media("<wide") {
            width : 100%;
        }
        @include media("<medium") {
            padding-left : 1rem;
            padding-right : 1rem;
        }

        .logo {
            font-size : 1.5rem;
            font-weight : 600;
            color : white;
        }

        .buttons {
            margin-left : auto;
        }
    }
}
