@import "../../styles/utils.module.scss";

.post-list-item {
    padding : 1.5rem;
    transition : all .15s ease-in;

    .title {
        font-size : 2rem;
        font-weight : 400;
        margin : 0;
        color : $oc-gray-8;

        a {
            transition : all .15s ease-in;
            border-bottom : 1px solid transparent;
            &:hover {
                color : $oc-blue-6;
                border-bottom : 1px solid $oc-blue-6;
            }
        }
    }

    .date {
        font-size : 0.85rem;
        color : $oc-gray-5;
    }

    .content {
        font-weight : 300;
        color : $oc-gray-7;
    }

    .tags {
        font-size : 0.85rem;
        color : $oc-blue-6;

        a {
            &:hover {
                color : $oc-blue-5;
                text-decoration : underline;
            }
        }

        a + a {
            margin-left : 0.25rem;
        }
    }

    &:hover {
        background-color : rgba($oc-blue-6, 0.05);
    }
}
