@import "../../styles/utils.module.scss";

.post-header {
    background-color: $oc-blue-6;
    height: 24rem;
    display: flex;
    align-items: center;
    justify-content: center;

    .info {
        margin-top: -5rem;
        width: 1024px;
        padding: 1rem;
        color : white;

        h1 {
            font-weight: 300;
            font-size: 3rem;
            margin: 0;
            word-wrap: break-word;
        }

        .tags {
            margin-top: 1rem;
            font-size: 1.25rem;
            font-weight: 500;

            a {
                &:hover {
                    text-decoration: underline;
                }
            }

            a + a {
                margin-left: 0.25rem;
            }
        }

        .date {
            text-align: right;
            opacity: 0.75;
            font-style: italic;
            font-size: 1.25rem;
        }
    }

    @include media("<large") {
        .info {
            h1 {
                font-size: 2rem;
            }

            .tags, .date {
                font-size: 1rem;
            }

            width: 768px;
        }
    }

    @include media("<medium") {
        height: auto;
        padding-bottom: 4rem;

        .info {
            padding-top: 0;
            margin: 0;

            .tags {
                margin-top: 0.25rem;
            }

            .tags, .date {
                font-size: 0.85rem;
            }
        }
    }
}
