@import "../../../styles/utils.module.scss";

.sign-in {
    background-color : white;
    padding : 2rem;
    position : relative;
    padding-top : 2.5rem;
    width : 20rem;

    .close {
        line-height : 2rem;
        font-size : 2rem;
        position : absolute;
        right : 1rem;
        top : 0.5rem;
        cursor : pointer;
        &:hover {
            color : $oc-gray-6;
        }
    }

    .title {
        font-size : 1.25rem;
        font-weight : 500;
    }

    .message {
        margin-top : 0.25rem;
    }

    .error {
        margin-top : 0.5rem;
        margin-bottom : 0.5rem;
        color : $oc-red-6;
        text-align : center;
        font-size : 0.85rem;
    }

    input {
        width : 100%;
        font-size : 1.25rem;
        margin-top : 0.5rem;
        border : none;
        border-bottom : 1px solid $oc-gray-3;
        padding : 0.25rem;
        outline : none;
        border-radius : 4px;
    }

    .button {
        background-color : $oc-blue-6;
        text-align : center;
        color : white;
        font-weight : 500;
        padding-top : 0.5rem;
        padding-bottom : 0.5rem;
        cursor : pointer;
        margin-top : 1rem;
        font-size : 1.25rem;
        &:hover {
            background-color : $oc-blue-5;
        }
        &:active {
            background-color : $oc-blue-6;
        }
    }
}
